import React from "react";
import CardListing from "../../components/CardList";
import { Box, Anchor } from "grommet";

const Home = ({ stores, publicPage }) => {
  const displayAnchor = (e, isPublic) =>
    isPublic ? (
      <Anchor href={`/store/${e.get("uniqueName")}`}>Go to Store</Anchor>
    ) : (
      <>
        <Anchor href={`/chat/store/${e.id}`}>Chat</Anchor>
        <Anchor href={`/account/store/editstore/${e.id}`}>Edit</Anchor>
        <Anchor href={`/account/store/manageproduct/${e.id}`}>
          Manage Product
        </Anchor>
        <Anchor href={`/account/store/manageproduct2/${e.id}`}>
          Manage Product (New)
        </Anchor>
      </>
    );
  return (
    <Box align="center" alignContent="center" justify="center">
      <CardListing
        dataList={stores.map((e) => ({
          title: e.get("name"),
          cta: publicPage ? (
            <Anchor>Go to Store</Anchor>
          ) : (
            displayAnchor(e, publicPage)
          ),
          cardHref: publicPage ? `/store/${e.get("uniqueName")}` : undefined,
        }))}
      />
    </Box>
  );
};

export default Home;
