import React, { useEffect, useState } from "react";
import { Box, Image, Anchor } from "grommet";
import DropDownZone from '../DropZone'
const Component = ({ files }) => {
  const [fileArray, setFileArray] = useState(files)
  const [activeIndex, setActiveIndex] = useState(0);
  const [activeFile, setActiveFile] = useState(null)

  useEffect(() => {
    if(activeIndex < fileArray.length && activeIndex > -1) {
      setActiveFile(fileArray[activeIndex])
    }
  }, [activeIndex])
  return <Box>
    <DropDownZone
      onChange={(files, newFile) => {
        setFileArray(
          fileArray.concat([newFile])
        )
      }}
    />
    <Box 
      width={{ 'min': "300px", max: '600px' }}
      height="340px"
      overflow="hidden"
    >
    {
      activeFile && <Image 
            fit="cover" 
            src={activeFile.url()} />
    }
    </Box>
    <Box direction="row" gap="small">
      <Box>
      <Anchor onClick={() => activeIndex - 1 > -1 && setActiveIndex(activeIndex - 1)}>Prev</Anchor>
      </Box>
      <Box margin={{ left: 'auto' }}>
        <Anchor onClick={() => activeIndex + 1 < fileArray.length && setActiveIndex(activeIndex + 1)}>Next</Anchor>
      </Box>
    </Box>
  </Box>

}

export default Component;
